/* Google Font */
@import url('https://fonts.googleapis.com/css2?family=Nunito+Sans:ital,wght@0,400;1,900&display=swap');
 
body {
  --color-dark:#27292a; 
  --color-darkest:#1f2122;
  --color-light:#fff;
  --color-primary:#ec6090;
  --color-icon:yellow;

  --color-bg-body:var(--color-darkest);

  --distance-primary:50px;
  --padding-primary:var(--distance-primary);
  --margin-primary:var(--distance-primary);
  --border-radius-primary:calc(var(--distance-primary)/2);


  --font-wight-bold:900;
  --font-wight-normal:400;
  --font-family: 'Poppins', sans-serif;

  background-color:var(--color-bg-body);
  font-family: var(--font-family);
   
 
}
ul{
  list-style-type: none;
}
a{
  text-decoration: none;
}
