 

.hero-main{
    background :url(../../assest/img/banner-bg.jpg) no-repeat top right/cover ;
    background-color: var(--color-dark);
    border-radius: var(--border-radius-primary);
    color: var(--color-light);
    padding: var(--padding-primary);
}



.hero-title{
    font-size: 60px;
    text-transform: uppercase;
    margin-bottom: var(--margin-primary);
    font-weight: var(--font-wight-bold);
     
}
.hero-title em{
    font-style: normal;
    color:var(--color-primary);
}

.her-sub-title{
    font-size:20px;
    color: var(--color-light);
    font-weight: var(--font-wight-normal);
    margin-bottom: calc(var(--margin-primary)/2);
}

@media (max-width: 767px){
    .hero-title{
        text-align: center;
        justify-content: center;
        display: flex;
        flex-wrap: wrap;
        font-size: 35px;
        text-transform: uppercase;
        margin-bottom: var(--margin-primary);
        
    }
    .hero-text{
        text-align: center;
        justify-content: center;
    } 
}