.gaming-library-card{
    flex: 1 1 100%;
    
}
.gaming-library-card ul{
    display: flex;
    justify-content: space-between;
    align-items: center;
    border-bottom: 1px solid var(--color-primary);
    padding: calc(var(--padding-primary)/2) 0;
    flex-wrap: wrap;
    gap: 15px 10px;
}
.gaming-library-card ul li span{
    color: #666;
}


.gaming-library-card ul h4{
    font-size: 1rem;
    font-weight: var(--font-wight-bold);
}
.gaming-library-card ul img{
    border-radius: var(--border-radius-primary);
}


@media (max-width: 767px){
    .gaming-library-card ul{
        display: flex;
        justify-content: center;
        align-items: center;

    }


    .gaming-library-card ul li {
        flex: 1 1 40%;
        font-size: 13px;
        text-align: center !important;

    }
    .gaming-library-card ul div{
        display: flex;
        flex:  1 1 100%;
        justify-content: center;
        align-items: center !important;

    }
    .gaming-library-card ul h4{
        font-size: 0.8rem;
        font-weight:bold;
    }
    .gaming-library-image{
        display: flex;
        flex: 1 1 100% !important;
        align-items: center !important;
        justify-content: center;
        

    }
    .gaming-library-image img{
        max-width: 80px;
        
      
         
    }
}