p {
    text-align: center;
    padding: calc(var(--padding-primary)/2);
    color: var(--color-light);
    font-weight: var(--font-wight-normal);
}


.cg {
    color: var(--color-primary);
    transition: all .3s;
}

.cg:hover{
    color: var(--color-light);
    background-color: var(--color-primary) ;
}