.button a {
    font-size: 14px;
    color: #fff;
    padding: 12px 30px;
    border-radius: 25px;
    font-weight: 400;
    letter-spacing: 0.5px;
    transition: all .3s;
     
}

.primary-btn a{
    background-color: var(--color-primary);

}
.primary-btn a:hover{
    background-color:var(--color-light) ;
    color: var(--color-primary);
}

.secondary-btn a{
    border :1px solid #666;
    color: #666;
     
}
.secondary-btn a:hover{
    border: 1px solid var(--color-light) ;
    color: var(--color-primary);
    background-color: var(--color-light);
}